import React, {lazy, Suspense, } from "react";
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';
import { Routes, Route, Link } from "react-router-dom";
import OnClickHandler from "./onClickHandler";
import Swal from 'sweetalert2';

const Home = lazy(() => import('./components/Home'));
const About = lazy(() => import('./components/About'));

const App = () => {

const [count, setCount] = React.useState(0);
const [hydrated, setHydrated] = React.useState(false);

  React.useEffect(() => {
    setHydrated(true); // Ensures the client has mounted before interacting
  }, []);

  const increment = () => {
    setCount(count + 1);
  };

  const decrement = () => {
    setCount(count - 1);
  };

    return (
    <div>
      <nav>
        <Link to="/">Home</Link> | <Link to="/about">About</Link>
      </nav>
      <Divider />
      <Stack direction="row" spacing={2}>
        <p>{hydrated ? count : 0}</p>
        <Button variant="contained" size="small" onClick={increment}>
          Increment
        </Button>
        <Button variant="contained" size="small" onClick={decrement}>
          Decrement
        </Button>
      </Stack>
   
          <Routes>
            <Route path="/" element={
              <>
                <OnClickHandler />
                <Home /> 
              </> 
              } />
            <Route path="/about" element={<About />} />
            <Route path="*" element={<><div>Oops!... <h3><strong>404</strong></h3></div></>} />
          </Routes>
   
    </div>
  );
};

export default App;

